html {
  overflow: hidden;
  height: 100%;
}
body {
  height: 100%;
  overflow: auto;
  margin: 0;
  padding: 0;
  background-color: #c3d1d321;
}

#root {
  padding: 0px;
}

.registration {
  padding-top: 30px;
  text-align: center;
}
.button_auth {
  display: block;
  width: 300px;
  height: 35px;
  margin: 0 auto 50px auto;
  background-color: #3897f0;
  border: none;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  font-size: 20px;
  transition: all 0.15s;
}

.container-my {
  width: 1230px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
}
.header {
  background-color: #fff;
  height: 100px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.0975);
}
.header .container-my {
  align-items: center;
  justify-content: center;
  height: 100%;
  width: auto;
}
.container-my h1 {
  text-align: center;
  font-size: 25px;
  font-weight: 700;
  color: #333;
  margin: 0;
  display: block;
}
.content {
  padding-top: 40px;
  padding-bottom: 40px;
}
.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
}
.item {
  width: 30%;
  margin-right: 3%;
  margin-bottom: 3%;
  height: 330px;
  overflow: hidden;
  position: relative;
  transition: all 0.15s;
  border: 1px solid rgba(0, 0, 0, 0.0975);
}
.item-info {
  width: 100%;
  height: 50px;
  box-sizing: border-box;
  background-color: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.0975);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}
.item-info a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #000;
}
.item-info a img {
  overflow: hidden;
  width: 32px;
  height: 32px;
  border-radius: 100%;
}
.item-info a span {
  margin-left: 5px;
}
.item-image {
  overflow: hidden;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
}
.item img {
  overflow: hidden;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
}
ul {
  list-style: none;
}
.load-more {
  display: block;
  width: 300px;
  height: 35px;
  margin: 0 auto 50px auto;
  background-color: #3897f0;
  border: none;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  font-size: 20px;
  transition: all 0.15s;
}

.card {
  width: 1230px;
  margin: 0 auto;
  padding-top: 30px;
  padding-bottom: 30px;
  text-align: center;
}
.card-info {
  padding: 20px;
  text-align: left;
}
.card-text {
  margin-bottom: 10px;
}
.button_go_back {
  background-color: #3897f0;
  border: none;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  padding: 8px 51px;
  font-size: 20px;
  transition: all 0.15s;
}
.button_go_back a {
  text-decoration: none;
}
.button_go_back a:visited {
  color: #fff;
}
.like-block {
  display: flex;
}
.likePhoto,
.dislikePhoto {
  border: none;
  background-color: transparent;
}
